.dashboard{
    background: url('../img/Page-Background.jpg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;

}
.multilevel,.motorchart{
    display: inline-block;
    margin-top: 10px;

}
.multilevel{
    margin-left: 30px;
}
.motorchart{
    float: right;
    margin-left: 30px;
}
.pressurechart{
    width: 500px;
    height: 500px;
    float: right;
    margin-left: 30px;
}